<template>
  <div id="app">
    <header>
      {{ msg }}
    </header>
    <div class="content">
      <router-view></router-view>
    </div>
    <footer>
      <div class="btn-group btn-group-lg" style="width:100%" role="group">

        <button type="button" class="btn btn-primary col-xs-6" v-for="item in routes" :key="item.key" v-on:click="goto(item.key)">{{item.value}}</button>
      </div>
    </footer>
  </div>
</template>

<script>
import router from './router/index';
export default {
  name: 'app',
  data() {
    return {
      msg: 'Winner v1.0',
      routes: [
        { key: '/', value: 'CalTool' },
        { key: 'MyRecord', value: 'Record' },
        { key: 'SIX', value: 'SIX' },
        { key: 'Record6', value: 'Record6' }
      ]
    };
  },
  methods: {
    hello: function() {
      router.push('/hello');
    },
    goto: function(routeName) {
      router.push(routeName);
    }
  },
  created: function() {
    document.body.style.height = document.body.clientHeight + 'px';
  }
};
</script>

<style lang="scss">
html,
body,
div,
button,
ul,
li {
  font-size: 100%;
  margin: 0;
  padding: 0;
  text-align: center;
  border: 0;
}
// reset 样式
html,
body {
  height: 100%;
}
#app {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.content {
  height: calc(100% - 132px);
  overflow-y: auto;
}
header {
  font-size: 2.4rem;
  padding-bottom: 10px;
  text-shadow: 0px 0px 1px red;
  height: 40px;
  line-height: 40px;
  /* background-image: url(./assets/images/band.png); */
  background-size: 100%;
  background: linear-gradient(#3496ff, #fff);
  background: -webkit-linear-gradient(#3496ff, #fff);
}

h1 {
  font-weight: normal;
  font-size: 2rem;
  /*background-color: white;
  border-bottom: 1px solid grey;*/
  padding-bottom: 10px;
  text-shadow: 0px 0px 1px red;
  height: 40px;
}

.normal-btn {
  min-width: 120px;
  height: 60px;
  background-color: #09bb07;
  padding: 0 30px;
  border: 0;
  border-radius: 6px;
  font-size: 13pt;
  color: white;
}

footer {
  .btn-group {
    .btn {
      border-radius: 0 !important;
    }
    .btn+.btn {
      margin-left: 0;
    }
  }
}
.my-content {
  overflow-y: auto;
  height: 100%;
  background-color: beige;
  .top {
    max-height: calc(100% - 44px);
    overflow-y: auto;
  }
  .button-area {
    padding: 4px 0px;
    border-top:2px solid #e4e4e4;
    background-color: #f1f1f1;
  }
}
</style>



