import Vue from 'vue';
import Router from 'vue-router';
import MyRecord from 'components/MyRecord';
import Log from 'components/Log';
import Mycal from 'components/Mycal';
import SIX from 'components/SIX';
import Record6 from 'components/Record6';
import Chart2 from 'components/Chart2';

Vue.use(Router);

export default new Router({
  routes: [
    // {
    //   path: '/hello',
    //   name: 'Hello',
    //   component: Hello
    // },
    {
      path: '/myrecord',
      name: 'MyRecord',
      component: MyRecord
    },
    {
      path: '/log',
      name: 'Log',
      component: Log
    },
    {
      path: '/',
      name: 'Mycal',
      component: Mycal
    },
    // {
    //   path: '/myCharts',
    //   name: 'MyCharts',
    //   component: MyCharts
    // },
    // {
    //   path: '/mytest',
    //   name: 'MyTest',
    //   component: MyTest
    // },
    {
      path: '/six',
      name: 'SIX',
      component: SIX
    },
    {
      path: '/record6',
      name: 'Record6',
      component: Record6
    },
    {
      path: '/chart2',
      name: 'Chart2',
      component: Chart2
    }
  ]
});



